.Form {
  background-color: #24262b;
  text-align: center;
}

.Form > h1 {
  color: red;
  margin: 0;
}

form {
    justify-content: center;
    text-align: -webkit-center;
    padding: 5vw;
    display: flex;
    flex-direction: column;
    background: #4285f4;
}

form > input {
    max-width: 400px;
    border: none;
    border-radius: 10px;
    padding: .5em 2em;
    height: 3em;
    margin-bottom: 1em;
    background: #004dffa3;
    font-size: 1.3em;
    color: #fff;
}

form > input[type="submit"] {
    color: #fff;
    max-width: 480px;
    border: none;
    border-radius: 10px;
    padding: .5em 2em;
    height: 3em;
    margin-bottom: 3em;
    background-color: #24262b;
    font-size: 1.3em;
    resize: none;
    
}



form > input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #2196F3;
    opacity: 1; /* Firefox */
  }
form > input:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #2196F3;
  }
  
form > input::-ms-input-placeholder { /* Microsoft Edge */
    color: #2196F3;
}

form > textarea {
    color: #fff;
    max-width: 400px;
    border: none;
    border-radius: 10px;
    margin-bottom: 3em;
    background: #004dffa1;
    padding: 1.2em 2em;
    height: 200px;
    font-size: 1.3em;

}

form > textarea::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: #2196F3;
    opacity: 1; /* Firefox */
  }
form > textarea:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: #2196F3;
  }
  
form > textarea::-ms-input-placeholder { /* Microsoft Edge */
    color: #2196F3;
}